import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

var model3;
var results_chart;

var da_counts = [0,0,0,0,0,0,0,0];
var all_text = ""
var in_meeting = 0;
var language = "en"

try {

var SpeechRecognition = window.webkitSpeechRecognition;
  
var recognition = new SpeechRecognition();

recognition.continuous = true;

recognition.onresult = function(event) {
  var current = event.resultIndex;
  var transcript = event.results[current][0].transcript;
  all_text += ";" + transcript
  var classname = classify_speech(transcript)
};

recognition.onstart = function() { 
  console.log('Voice recognition is ON.');
}

recognition.onspeechend = function() {
  console.log('No activity.');
  recognition.stop();
  if (in_meeting == 1){
  setTimeout(
    function() {
      recognition.start();
      console.log("speech started")
    }, 500);
  }
}

recognition.onerror = function(event) {
  if(event.error == 'no-speech') {
    console.log('Try again.');  
    recognition.stop();
    if (in_meeting == 1){
      setTimeout(
        function() {
          recognition.start();
          console.log("speech started")
        }, 500);
    }
  }
}

} catch(err) {
  console.log(err.message);
}

function startMeeting() {
  recognition.start();
  in_meeting = 1;
}

function stopMeeting() {
  recognition.stop();
  in_meeting = 0;
}

function resetMeeting() {
  da_counts = [0,0,0,0,0,0,0,0];
  all_text = "";
  updateFeedback();
}

function updateFeedback() {
  results_chart.data.datasets[0].data = da_counts;
  results_chart.update();
}

function classify_speech(sentence) {
   if (language != "en") {
      //first we translate the sentence
      var apiURL = "https://translate.googleapis.com/translate_a/single?client=gtx&sl="+language+"&tl=en&dt=t&q=" + encodeURI(sentence)
      var foreign = sentence;
      fetch(apiURL).then((response) => response.json())
        .then((responseData) => {
          sentence = responseData[0][0][0]//this is now the translated sentence

        //this is only english
        sentence = sentence.replace("!", ".");
        sentence = sentence.replace("?", ".");
        var sentences = sentence.split(".");
        sentences = sentences.filter(function(e){return e}); 

        sentences.forEach(function(s){    
          model3.classify([s])
          .then(predictions => {
            var ind_mapping = [0,5,2,3,6,4,1,7]
            var preds = []
            var maxi = 0
            var maxv = 1
            var probas = [];

            for(var i = 0; i < 8; i++) {
              if (predictions[i].results[0].probabilities[0] < maxv){
                maxi = i
                maxv = predictions[i].results[0].probabilities[0]
              }
              console.log(predictions[i].results[0].probabilities[0])
              probas.push(predictions[i].results[0].probabilities[0])
            }
            console.log("--")
            var prediction = ind_mapping[maxi]
            da_counts[prediction] += 1
            uploadDataToServer("speech:"+language+":"+foreign+";"+s,probas)
            updateFeedback();
            return true;
          });
        });
      });
    } else {
      //this is only english
      model3.classify([sentence])
      .then(predictions => {
        var ind_mapping = [0,5,2,3,6,4,1,7]
        var preds = []
        var maxi = 0
        var maxv = 1
        var probas = [];

        for(var i = 0; i < 8; i++) {
          if (predictions[i].results[0].probabilities[0] < maxv){
            maxi = i
            maxv = predictions[i].results[0].probabilities[0]
          }
          console.log(predictions[i].results[0].probabilities[0])
          probas.push(predictions[i].results[0].probabilities[0])
        }
        console.log("--")
        var prediction = ind_mapping[maxi]
        da_counts[prediction] += 1
        uploadDataToServer("speech:"+sentence,probas)
        updateFeedback();
        return true;
      });
    }
}

function uploadDataToServer(text,probabilities) {
    var d = new Date();
    var n = d.getTime();

    var apiURL = 'https://script.google.com/macros/s/AKfycbwK3EEp-8NU7wLEXLrZesp7XcjUYh7H6zdgQj7vBfDpDlwa13gl/exec?'
    apiURL += "timest="+n
    apiURL += "&sentence="+text
    apiURL += "&bd="+probabilities[0]
    apiURL += "&cmd="+probabilities[1]
    apiURL += "&lp="+probabilities[2]
    apiURL += "&nt="+probabilities[3]
    apiURL += "&neutral="+probabilities[4]
    apiURL += "&qu="+probabilities[5]
    apiURL += "&rf="+probabilities[6]
    apiURL += "&up="+probabilities[7]

    const formData = {};
    console.log("uploading data")
    fetch(apiURL).then((response) => response.text())
      .then((responseData) => { 
        console.log("response: ");
        console.log(responseData); })
      .catch((err) => { 
        console.log(err); 
      });
}

